	module.exports = "<h2 id=what-is-the-minimum-map-i-can-build->What is the minimum map I can build?</h2> <p>Let&#39;s get right to it and style the most basic map we can build with Tangram.</p> <p>To create a map, the scene file requires only:</p> <ul> <li>a data source (vector tiles)</li> <li>data interpretation rules (filters)</li> <li>styling rules (how the data should look on the map)</li> </ul> <div class=alert-message> We&#39;ll be using a special app that embeds Tangram code on the right and a Leaflet/Tangram map on the left to instantly view the results of our scene file. Take a look below! </div> <p>[section]</p> <p>Let&#39;s unpack this example. First of all, note that our <code>sources</code> and <code>layers</code> blocks are required elements for all valid Tangram scene files. Try commenting them out with <code>#</code> to see what happens. You should see the map on the left go black.</p> <h3 id=data-sources>Data sources</h3> <p>Let&#39;s talk about the <code>sources</code> block first. As the word indicates, this is the place where we define our data sources and tell the Tangram engine where to get its vector tiles from. The block takes only one type of parameter, a <strong>source name</strong>.</p> <pre><code class=language-yaml>sources:\n    &#95;mapzen: # A source name\n        type: GeoJSON\n        url: https://tangrams.github.io/tangram-tutorial/src/Assets/tutorial-files/countries.geojson\n</code></pre> <p>The source name is important because you will need to use it elsewhere in the Tangram scene file. Think of it as a variable name that you can refer to again and again throughout the scene file. The <code>sources</code> block can take any number of source names, and each of those can be named anyway you&#39;d like.</p> <pre><code class=language-yaml>sources:\n    &#95;stamen: # A source name\n        type: Raster\n        url: http://a.tile.stamen.com/terrain-background/{z}/{x}/{y}.jpg\n    &#95;mapzen: # Another source name\n        type: GeoJSON\n        url: https://tangrams.github.io/tangram-tutorial/src/Assets/tutorial-files/countries.geojson\n</code></pre> <div class=alert-message>For this tutorial, non-required variable names will be prepended with a <code>_</code> to avoid confusion with reserved keywords. In the example above <code>_stamen</code> and <code>_mapzen</code> can be thought of as variable names. Instead, <code>sources</code> and <code>layers</code> are reserved keywords.</div> <p>Each source name in turn requires two pieces of information:</p> <ul> <li>a <code>type</code>: the type of the datasource. It can be any one of four options:<ul> <li><code>TopoJSON</code></li> <li><code>GeoJSON</code></li> <li><code>MVT</code> (Mapbox Vector Tiles)</li> <li><code>Raster</code></li> </ul> </li> <li>a <code>url</code>: the data source&#39;s url</li> </ul> <p><br></p> <p>[section]</p> <h3 id=layers-and-data-import>Layers and Data Import</h3> <p>So how will we be using the data we just imported? Let&#39;s start by unpacking what we&#39;ve written in the <code>layers</code> block. Vector tiles typically contain top-level structures which can be thought of as layers  inside a GeoJSON file, these would be the FeatureCollection objects. Inside a Tangram scene file, the layers object allows you to split the data by layer, by matching against the layer name.</p> <p>[section]</p> <p>Now let&#39;s actually take a look at our data source, in this case <a href=https://tangrams.github.io/tangram-tutorial/src/Assets/tutorial-files/countries-data.geojson target=&#95;blank>https://tangrams.github.io/tangram-tutorial/src/Assets/tutorial-files/countries-data.geojson</a>. This is just a simple geojson file that represents every country in the world as a polygon or multipolygon. This code shows a small part of the file (note replacements where we&#39;ve simplified the file with <code>[...]</code>):</p> <p><pre><code class=language-json>{\n    &quot;countries&quot;: {\n        &quot;type&quot;: &quot;FeatureCollection&quot;,\n        &quot;features&quot;: [\n            {\n                &quot;type&quot;: &quot;Feature&quot;,\n                &quot;id&quot;: &quot;ARG&quot;,\n                &quot;properties&quot;: {\n                    &quot;name&quot;: &quot;Argentina&quot;,\n                    [...more properties...]\n                },\n                &quot;geometry&quot;: {\n                    &quot;type&quot;: &quot;MultiPolygon&quot;,\n                    &quot;coordinates&quot;: [...coordinates...]\n                }\n            },\n            {\n                &quot;type&quot;: &quot;Feature&quot;,\n                &quot;id&quot;: &quot;AUS&quot;,\n                &quot;properties&quot;: {\n                    &quot;name&quot;: &quot;Australia&quot;,\n                    [...more properties...]\n                },\n                &quot;geometry&quot;: {\n                    &quot;type&quot;: &quot;MultiPolygon&quot;,\n                    &quot;coordinates&quot;: [...coordinates...]\n                }\n            },\n            [...other countries...]\n        ]\n    }\n}\n</code></pre></p> <div class=alert-message> An explanation of the GeoJSON and TopoJSON standards is beyond the scope of this tutorial. For a good resource check out: <a href=http://www.macwright.org/2015/03/23/geojson-second-bite.html>http://www.macwright.org/2015/03/23/geojson-second-bite.html</a> </div> <p>Note that we are specifying what layer name to match against on the highlighted line 12 in the next example. Try editing the Tangram scene file yourself on lines 8 and 12.</p> <p><pre><code class=language-yaml>&#95;countryLayer:\n        data:\n            source: &#95;mapzen\n            layer: countries # matching to the &#39;countries&#39; layer in the geojson file\n</code></pre></p> <p>[section]</p> <p>What else is going on here? How are we styling the countries layer? We are specifying how to display the countries layer within the <code>draw</code> element.</p> <p><pre><code class=language-yaml>draw:\n    &#95;countryStyle: # We&#39;re giving this draw group a name &#39;&#95;countryStyle&#39;\n        style: polygons # Tangram comes with a prebuilt set of styles for different data types: points, polygons, lines, text, and raster\n        color: darkgreen # The color of the layer we are drawing\n</code></pre></p> <p>How do we know what style to apply to what data? Well our geojson file contains some clues. Take another look at the file:</p> <p><pre><code class=language-json>[...]\n{\n    &quot;type&quot;: &quot;Feature&quot;,\n    &quot;id&quot;: &quot;ARG&quot;,\n    &quot;properties&quot;: {\n        &quot;name&quot;: &quot;Argentina&quot;,\n        [...more properties...]\n    },\n    &quot;geometry&quot;: {\n        &quot;type&quot;: &quot;MultiPolygon&quot;,\n        &quot;coordinates&quot;: [...coordinates...]\n    }\n}\n[...]\n</code></pre></p> <p>Each country is composed of a geometry which in this case is of type <code>MultiPolygon</code> but following the geojson specs could be a <code>Point</code>, <code>LineString</code>, <code>Polygon</code>, <code>MultiPoint</code>, or <code>MultiLineString</code>. Since each country is a multipolygon, we&#39;re styling the &#39;countries&#39; layer with a <code>polygon</code> style. Try changing the styles in the following scene file to see what happens:</p> <p>[section]</p> <p>Tangram did not interpret the data correctly when using a <code>text</code> or <code>raster</code> style because the data is not encoded as any of these. As a consequence, the map renders nothing at all. For <code>lines</code> we need to add an extra element we&#39;ll go into in a bit. On the other hand when we use <code>points</code> Tangram is rendering the vertices (or points) of each polygon in our &#39;country&#39; layer.</p> <div class=alert-message> The TopoJSON and MVT (Mapbox Vector Tiles) are structured in a similar way to GeoJSON files, so these geometric properties still apply. Raster files are different and we will cover them later on in the tutorial. </div>";

