	module.exports = "<h2 id=styling>Styling</h2> <p>Let&#39;s talk about styling with <code>lines</code> by adding country borders. Since our geojson represents each country as a polygon or multipolygon, we know that there are edges (or lines) in between each vertex (or point). We can use the <code>lines</code> style to draw these borders. And since we&#39;re using the same layer of data (&#39;&#95;countryStyle&#39;), we can simply declare a new style under the <code>draw</code> block.</p> <p>[section]</p> <h2 id=layer-order>Layer Order</h2> <p>Let&#39;s talk about ordering our layers for styling. <code>order</code> is a parameter that sets the drawing order of the draw style. It is meant to be used in cases where layers cover the same pixel space, meaning that one layer will take precedence over another when they get drawn. You can use the <code>order</code> parameter to specify which higher-ordered layers should be drawn over lower-ordered layers. Try the exercise below:</p> <p>[section]</p> <h2 id=colors>Colors</h2> <p>Tangram gives you complete control over how you might choose to color a scene. With that in mind, we support a variety of color formats such as:</p> <h3 id=1-css-colors>1. CSS colors</h3> <p>A variety of CSS color formats are available, as specified in the <a href=http://www.w3schools.com/cssref/css_colors_legal.asp>W3Cs Cascading Style Sheets specification</a>:</p> <ul> <li>named colors: <code>red</code>, <code>blue</code>, <code>salmon</code>, <code>rebeccapurple</code></li> <li>hex colors: <code>&quot;#fff&quot;</code>, <code>&quot;#000&quot;</code>, <code>&quot;#9CE6E5&quot;</code></li> <li>RGB colors: <code>rgb(255, 190, 0)</code></li> <li>RGBA colors: <code>rgb(255, 190, 0, .5)</code></li> <li>HSL colors: <code>hsl(180, 100%, 100%)</code></li> <li>HSLA colors: <code>hsla(180, 100%, 100%, 50%)</code></li> </ul> <p>For example:</p> <p><pre class=no-margin><code class=language-yaml>draw:\n    polygons:\n        color: &#39;#ff00ff&#39;\n</code></pre></p> <p><pre class=no-margin><code class=language-yaml>draw:\n    polygons:\n        color: hsl(180, 100%, 100%)\n</code></pre></p> <h3 id=2-vector-format>2. Vector format</h3> <p>In this format RGB/RGBA values range from 0-1.</p> <ul> <li>Representing RGB: <code>[0.7, 0.7, 0.7]</code></li> <li>Representing RGBA: <code>[1.0, 0.2, 0.3, 0.5]</code> 0.5 here represents 50% alpha</li> </ul> <p>For example:</p> <p><pre class=no-margin><code class=language-yaml>draw:\n    polygons:\n        color: [1.0, 0.2, 0.3, 0.5]\n</code></pre></p> <h3 id=3-as-a-function>3. As a function</h3> <p>For example:</p> <p><pre class=no-margin><code class=language-yaml>draw:\n    polygons:\n        color: function() { return [$zoom, .5, .5]; }\n</code></pre></p> <div class=alert-message> The keyword $zoom is built into Tangram and matches the current zoom level of the map. </div> <p>Let&#39;s try these examples out:</p> <p>[section]</p> <h2 id=labels>Labels</h2> <p>Finally, let&#39;s introduce one more styling concept before we jump into data filters. Remember the <code>text</code> style that we spoke about in the <a href=/#/minimum-map/min>Layers and Data Import</a> section? Well we can use that style for showing labels! Let&#39;s do that for our countries.</p> <p>First, there&#39;s one line of magic sauce we need to add to our <code>sources</code> block in the form of <code>generate_label_centroids: true</code> (on line #5 below). This line tells Tangram to add new points to the data source, each located at the geometrical center (or &quot;centroid&quot;) of every polygon in the data. We will use these new points for positioning our labels.</p> <p>Second, we need to add another layer name with details for how to draw those labels. Let&#39;s call this layer <code>_textLayer</code> (line #23). Recall that all layers are composed of a <code>data</code> and <code>draw</code> block (<a href=/#/minimum-map/min>Layers and Data Import</a>) so let&#39;s add that information into our scene file (lines #24-33).</p> <ul> <li><code>data</code> block: this block will be the same as the one for our <code>_countryLayer</code> style. We&#39;re using the same data source!</li> <li><code>draw</code> block: here&#39;s where the rest of the labeling magic happens.<ul> <li><code>style</code>: recall that we can choose from <code>points</code>, <code>polygons</code>, <code>lines</code>, <code>text</code>, and <code>raster</code>. Since we want to display a text label, let&#39;s choose <code>text</code>.</li> <li><code>font</code>: specifies the font for the label.</li> <li><code>text_source</code>: this parameter defines the source of the label. How do we know what source to use? Let&#39;s take another look at our geojson file on line #4.</li> </ul> </li> </ul> <p><pre><code class=language-json>[...]\n{\n    &quot;type&quot;: &quot;Feature&quot;,\n    &quot;id&quot;: &quot;ARG&quot;,\n    &quot;properties&quot;: {\n        &quot;name&quot;: &quot;Argentina&quot;,\n        &quot;currencyCode&quot;: &quot;ARS&quot;,\n        &quot;population&quot;: &quot;41343201&quot;,\n        &quot;capital&quot;: &quot;Buenos Aires&quot;,\n        &quot;continentName&quot;: &quot;South America&quot;,\n        &quot;areaInSqKm&quot;: &quot;2766890.0&quot;,\n        &quot;languages&quot;: &quot;es,en,it,de,fr,gn&quot;\n    },\n    &quot;geometry&quot;: {\n        &quot;type&quot;: &quot;MultiPolygon&quot;,\n        &quot;coordinates&quot;: [...coordinates...]\n    }\n}\n[...]\n</code></pre></p> <p>Essentially anything within the <code>properties</code> section can be used as a valid <code>text_source</code> as long as it is a string. If we use <code>text_source: name</code> as in the example below, Tangram will match the <code>name</code> property in the geojson and serve that as a label.</p> <div class=alert-message> All valid GeoJSON and TopoJSON files have a type member with the name &quot;properties&quot; - it&#39;s not something particular to the GeoJSON file we are using in this example. Mapbox Vector Tiles also have a &quot;properties&quot; member. </div> <p>[section]</p> <p>Oh no! Take a close look at the map - what&#39;s going on with all the repeated labels (zoom in closer if you can&#39;t see them)? This is where we go back to the magic sauce <code>generate_label_centroids: true</code> on line 5.</p> <p>For each point that Tangram has generated at the centroid of each polygon, it has also created a <code>{&quot;label_placement&quot; : &quot;yes&quot;}</code> property. This is how we&#39;ll filter for just those points within our <code>_labelStyle</code> style. To do so we need something called a <code>filter</code> block - which we&#39;ll go into later. For now, just note the additional lines 33-34 that filter just the points at the centroid of each polygon.</p> <div class=alert-message> If the feature in question is a multipolygon, the centroid point will be added to the largest polygon in the group. </div> <p>[section]</p> <h2 id=customizing-text>Customizing Text</h2> <p>Tangram is powerful because it let&#39;s you customize the drawing style of your layers to suit your needs. There are many more parameters you can use to customize your labels. Take a look at a few below:</p>";

