	module.exports = "<h2 id=vector-tiles>Vector Tiles</h2> <p>Congratulations! If you&#39;ve made it this far you&#39;ve learned how to build basic maps, a few styling rules, and the many uses of data filters. Now we&#39;re about to head into the heart of Tangram and learn how we can get free data about any place on earth.</p> <p>Let&#39;s go back to the map we&#39;ve been building in previous sections. We&#39;ve managed to draw our countries, but what if we wanted to color the oceans? With our current GeoJSON file we can&#39;t really do that, because the only data it provides is countries (represented as polygons). This is where vector tiles come in.</p> <p>The <a href=https://mapzen.com/projects/vector-tiles/ target=&#95;blank>Mapzen vector tile service</a> provides worldwide basemap coverage sourced from <a href=http://www.openstreetmap.org/ target=&#95;blank>OpenStreetMap</a> and other open data projects, updated daily as a free &amp; shared service.</p> <h3 id=what-is-a-vector-tile->What is a vector tile?</h3> <p>Simply put, vector tiles are squares of math. Instead of a web server sending you a pre-drawn image tile, a vector tile contains the geometry for a particular part of the earth, delivered on demand. It contains instructions on what can be drawn, rather than something already drawn and styled. Below is a snippet of a vector tile in GeoJSON; as you can see it contains mathematical descriptions and metadata of buildings, roads and other features.</p> <p>[section]</p> <p>How to draw the tile is up to the vector-friendly visualization tool (usually SVG, Canvas, WebGL, or Tangram) in the web browser. Buildings and roads can be rendered in different ways (or not at all) once the vector tile is downloaded  no need to head back to the server and fetch a different set of tiles if you want to filter the output. Theres no need to re-render millions of tiles if you change your mind about the style of a road or the color of a building.</p> <h3 id=a-simple-map-using-vector-tiles>A simple map using vector tiles</h3> <p>With that in mind let&#39;s take a look at a simple map we can build with the Mapzen Vector Tile service and see how simple it is to obtain the data we need to color oceans:</p> <p>[section]</p> <p>How did we get the earth and water layer?</p> <p>TODO: talk about OSM layers stuff we&#39;re feeding</p> <h3 id=a-reminder-about-order>A reminder about order</h3> <p>Remember that layer order matters when we&#39;re styling a map. Take a look at what happens if we revert the water and earth layer orders. If we place the earth layer (order 1) on top of the water layer (order 0) then we wouldn&#39;t get any landlocked bodies of water like lakes and rivers!</p> <p>[section]</p> <h3 id=url-syntax>URL syntax</h3> <p>Notice that our source URL looks a little different than the GeoJSON and TopoJSON we&#39;ve been using so far. It&#39;s because we&#39;re linking directly to the Mapzen Vector Tile service, which provides vector tiles in a variety of formats. The following three sources are equivalent in the data that they provide; what&#39;s different is the encoding format of that data.</p> <p><code>http://vector.mapzen.com/osm/all/{z}/{x}/{y}.topojson</code> TopoJSON format</p> <p><code>http://vector.mapzen.com/osm/all/{z}/{x}/{y}.json</code> GeoJSON format</p> <p><code>http://vector.mapzen.com/osm/all/{z}/{x}/{y}.mvt</code> Mapbox Vector Tiles format</p> <div class=alert-message> We recommend TopoJSON format for desktop web development, and MVT format for native mobile development. The Mapzen server gzips tiles automatically, so the TopoJSON file format is comparable in file size to MVT over the wire, and its much friendlier to debug. </div> <h3 id=sourcing-individual-layers>Sourcing Individual Layers</h3> <p>In reality you can also replace the <code>all</code> in the url with any number of layers of your choice. Layer names filter the geometry that is delivered: buildings and roads and water are but a few examples. Multiple layer names can be combined in the URL, such as roads and water:</p> <p><code>https://vector.mapzen.com/osm/earth/{z}/{x}/{y}.topojson</code></p> <p>or</p> <p><code>https://vector.mapzen.com/osm/earth,water/{z}/{x}/{y}.topojson</code> </p> <p><br> Importantly, though, if you only import one layer, you must leave out the <code>data: layer</code> filter. Otherwise you can apply the data filter as usual. Take a look at the two examples below:</p> <p>[section]</p> <h3 id=api-keys>API keys</h3> <p>One last comment about URLs. We&#39;ve been using a special app in this tutorial that let&#39;s us embed Tangram and scene file code in one single view. If you go on to deploy a Tangram scene file on your own, you will need an API key. API keys come in the pattern: <code>vector-tiles-xxxxxxx</code> and can be obtained here: <a href=https://mapzen.com/developers/sign_in>https://mapzen.com/developers/sign_in</a>. A full URL pattern would look something like this, where you should replace <code>xxxxxxx</code> with your particular API key:</p> <p><code>https://vector.mapzen.com/osm/all/{z}/{x}/{y}.topojson?api_key=vector-tiles-xxxxxxx</code></p> <div class=alert-message> If you&#39;re curious, here is what a single sample TopoJSON tile looks like: <code>https://vector.mapzen.com/osm/all/16/19293/24641.topojson?api_key=vector-tiles-xxxxxxx</code> </div> <p>[section]</p> <p>-What is included in the vector tiles? How to explain difference with Mapbox? <em> Renamed many things from OSM. Based off of. </em> POI: minutely feed - tile build queue. Hours/Days. Check Rob with most current up to date. <em> Mapbox Vector Tiles - MVT smaller payload. Have to do some more processing on client side. Protocol buffers. -Can I she sample of the {x},{y} -API keys? </em> Tutorials Key -what do you think are most important distinctions here? https://mapzen.com/documentation/vector-tiles/layers/</p>";

